@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  color:#fff;
}

h1{
  color:#ffff;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

h1 {
  color: #ffff;
  font-size: 2rem;
  font-family: 'Roboto', sans-serif;
}
ul a {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}
body a:hover {
  transition: color 0.4s;
  color: #92c73e;
  text-decoration: none;
}
button {
  background: none;
  text-decoration: none;
  color: #fff;
  outline: none;
}

button:hover {
  border: none;
  text-decoration: none;
  border-radius: none;
  outline: none;
}
button:active {
  border: none;
  text-decoration: none;
  border-radius: none;
  outline: none;
}

button:focus {
  border: none;
  text-decoration: none;
  border-radius: none;
  outline: none;
  outline-style: none;
  outline-width: 0px !important;
  outline-color: none !important;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.row span {
  color: #fff;
}
span,
a {
  font-family: "Roboto", sans-serif;
}

.headerRow {
  text-align: center;
  min-height: 56px;
  align-items: center;
  background: #304355;
  transition: all .7s;
}

.headerRow svg {
  margin-right: 8px;
}

.headerMiddle {
  padding: 35px 15px;
}

#root {
  overflow: hidden;
}

.FeedbackTextarea {
  min-height: 130px;
  width: 100%;
}

.justify-conent {
  justify-content: center;
}
.justify-content .form-check {
  align-self: center;
}
.titleModalWindowFeedBack {
  width: 100%;
  text-align: center;
  color: #1e3953;
}
.titleModalWindowFeedBack h4 {
  text-align: center;
}

button.btn.btn-secondary {
  background-color: #dc3545;
  border-color: #dc3545;
}

.backgroundWrapper {
  background-color: #1e3953;
  padding-top: 20px;
}
.backgroundWrapperCategories {
  background-color: #fff;
  padding-bottom: 20px;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
.footer a {
  color: #fff;
  box-sizing: border-box;
  min-height: 31px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer .col {

  display: flex;
  flex-direction: column;
  justify-content:space-around;
}

.footer-hr {
  min-height: 50px;
  text-align: center;
  line-height: 50px;
  background-color: #172b44;
  color: #fff;
}

.footerbottom-hr {
  min-height: 25px;
  background-color: #172b44;
}

@media (max-width: 768px) {
.footer-hr{
    line-height:25px;
}
.footerbottom-hr{
    display: none;
}
.footerMain{
    display:none!important;
}

}

.footerMain{
    font-size: 18px;
}

.scrolled{
    position: fixed;
    top: 0;
    width: 100%;
    display:flex;
    justify-content: space-around!important;
    z-index:210;
    margin: 0 !important;
    background-color:#304355;
    transition: all .7s;
  }

  .body-wrapper{
    padding: 50px 25px 0;
  }
 

