
.formControl.error input,
.formControl.error textarea
{
border: 1px red solid;

}

.formControl.error span{
    color: red;
    text-align: center;
}

.formControl label {
    font-weight: bold;
    margin-right: 10px;
    min-width: 180px;
    text-align: right;
}

.formControl input, textarea::placeholder{
    padding:5px 8px
}


.formControl {
    display: flex;
    margin: 10px 5px;
    flex-flow: row nowrap;
}
.formControl input, textarea{
    border:1px solid #1e3953;

    }
.LabelAndInput{
    flex: 1;
    max-width: 500px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
    flex-flow: column nowrap;
    position: relative;
}

.formControl textarea{
    min-height: 165px;
}
.formControl button{
   color: red;
}

@media (max-width: 768px) {

    .formControl {
    display: block;

    }
    .formControl input, textarea{
        border:1px solid #1e3953;
    
        }

    
    .formControl label{
        display: block;
            width: 100%;
            text-align: center;
        }
    }